@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500;700&display=swap');


* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Montserrat', sans-serif;
    font-size: 1rem;
    line-height: 1.6;
    background-color: var(--color-white);
    color: var(--color-dark);
}

a {
    color: inherit;
    text-decoration: none;
}

ul {
    list-style: none;
}

img {
    width: 100%;
}

span {
    vertical-align: middle;
}

/* Text Styles*/

h1,
h2,
h3,
h4,
h5 {
    font-family: 'Montserrat', Arial, Helvetica, sans-serif;
    font-weight: 600;
    line-height: 1.3;
    margin-bottom: 1rem;
}

h1 {
    margin-top: 0;
    font-size: 3.052rem;
}

h2 {
    font-size: 2.441rem;
}

h3 {
    font-size: 1.953rem;
}

h4 {
    font-size: 1.563rem;
}

h5 {
    font-size: 1.25rem;
}

p {
    margin-bottom: 1rem;
}



/* Homepage top header */
.homeheader {
    height: 65px;
    color: rgb(0, 0, 0);
    font-size: 50px;
    text-align: center;
    margin-top: 0;
    margin-bottom: 0;
}

/* Description bar below logo */
.homep1 {
    text-align: center;
    background-color: #28a745;
    color: whitesmoke;
    padding: 20px 20px 40px;
    margin-bottom: 0;
    margin-top: 0;
    height: 40px;
}

.imglanding {
    height: 500px;
    width: 500px;
}

.headerlogo {
    height: 100px;
    width: 100px;
    display: block;
    margin-left: auto;
    margin-right: auto;

}

.Landingbackground {
    z-index: 1;
    height: 510px;
    width: span;
}

.persononphone {
    z-index: 3;
    height: 510px;
    width: 505px;
    position: absolute;
    top: 225px;
    left: 1030px;
}

/* Signup Box */
.homeh1 {
    text-align: center;
    font-size: 30px;
    padding-top: 10px;
    height: 50px;
    bottom: 10px;
    color: #28a745;
    margin-bottom: 20px;
}

.formbox {
    width: 90%;
    max-width: 450px;
    position: absolute;
    top: 65%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #fff;
    padding: 50px 60px 50px;
    text-align: center;
}

.inputfield {
    padding-left: 70px;
    width: 150%;
    margin: 15px 0;
    border-radius: 0%;
    display: flex;
    align-items: center;


}

.input {
    width: 100%;
    background: transparent;
    border: 0;
    outline: 0;
    padding: 15px 15px;

}